import * as React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { GatsbyImage } from 'gatsby-plugin-image';
import * as styles from './PhotoCarousel.module.scss';
import { Carousel } from 'react-responsive-carousel/lib/js';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
export const PhotoCarousel = ()=>{
    const data = useStaticQuery(graphql`
    query ContactGallery {
      images: allFile(filter: { relativeDirectory: { eq: "assets/gallery" } }, sort: { relativePath: ASC }) {
        nodes {
          id
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH)
          }
        }
      }
    }
  `);
    return React.createElement("div", {
        className: styles.wrapper
    }, React.createElement(Carousel, {
        autoPlay: true,
        emulateTouch: true,
        transitionTime: 1000,
        interval: 3000,
        infiniteLoop: true,
        className: styles.carousel
    }, data?.images?.nodes?.map((image)=>{
        return React.createElement("div", {
            key: image.id,
            className: styles.slideWrapper
        }, React.createElement(GatsbyImage, {
            image: image.childImageSharp.gatsbyImageData,
            alt: "Alzahra Studio"
        }));
    })));
};
